@use '../foundation' as *;

.p-flow-list {
    @include mq(md) {
        max-width: rem(1130);
        margin: 0 auto;
    }
}

.p-flow__title {
    margin-bottom: rem(20);
    @include mq(md) {
        margin-bottom: rem(37);
    }
}

.p-flow-list__item {
    position: relative;
    &:not(:last-child) {
        margin-bottom: rem(73);
    }
    &::after {
        content: '';
        display: inline-block;
        background-image: url(./../img/common/triangle_under.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        width: rem(80);
        height: rem(47);
        position: absolute;
        bottom: -25%;
        left: 50%;
        transform: translateX(-50%);

        @include mq(mw) {
            background-image: url(./../img/common/triangle_under-sp.svg);
            width: rem(24);
            height: rem(46);
            bottom: -25%;
        }
    }
    &:last-child::after {
        content: none;
    }
    &:nth-child(3):after {
        bottom: -20%;
        @include mq(mw) {
            bottom: -20%;
        }
    }
}

.p-flow-list__item-wrap {
    padding: rem(32) rem(22) rem(22) rem(22);
    display: flex;
    align-items: center;
    flex-grow: 1;
    border: rem(5) solid $color-green;
    border-radius: rem(10);
    @include mq(md) {
        padding: rem(30) rem(24);
        padding-left: rem(51);
    }
}

.p-flow-list__item-box {
    @include mq(md) {
        margin-left: rem(66);
    }
}

.p-flow-list__item-title {
    font-size: rem(20);
    line-height: 1;
    font-weight: 700;
    margin-bottom: rem(28);
    margin-left: rem(50);
    position: relative;
    &::before {
        content: '';
        width: rem(30);
        height: rem(90);
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        top: 23%;
        left: -10%;
        transform: translate(-25%, -25%);
        @include mq(md) {
          transform: translate(-50%, -25%);
            left: -5%;
            width: rem(30);
            height: rem(30);
        }
    }
    @include mq(md) {
        margin-left: rem(0);
        font-size: rem(40);
        margin-bottom: rem(20);
    }
}

@include mq(mw) {
    .p-flow-list__item:nth-of-type(1) .p-flow-list__item-title::before {
        background-image: url(./../img/common/1.svg);
    }

    .p-flow-list__item:nth-of-type(2) .p-flow-list__item-title::before {
        background-image: url(./../img/common/2.svg);
    }

    .p-flow-list__item:nth-of-type(3) .p-flow-list__item-title::before {
        background-image: url(./../img/common/3.svg);
    }

    .p-flow-list__item:nth-of-type(4) .p-flow-list__item-title::before {
        background-image: url(./../img/common/4.svg);
    }
}

.p-flow-list__item-text {
}
